<template>
  <div class="houseManage">
    <el-row class="headerSearch">
      <el-col class="house_header_left header_hose" :span="7">
        <div style="margin-right:10px">楼盘名称:</div>
        <div>
          <el-input v-model="houseName" placeholder="请输入楼盘名称" suffix-icon="el-icon-search" />
        </div>
      </el-col>

      <el-col class="house_header_region header_hose" :span="7">
        <div style="margin-right:10px">所在地区:</div>
        <el-cascader v-model="houseArea" :options="regionList" @change="handleChange" />
      </el-col>

      <el-col class="header_hose" :span="7">
        <div style="margin-right:10px">状态:</div>
        <el-select v-model="houseTai" class="marleft" placeholder="请选择">
          <el-option
            v-for="item in option"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-col>
    </el-row>
    <el-row>
      <div style="width:100%;margin-top:10px;margin-left:20px">
        <el-button type="primary" @click.native="submit_search">查询</el-button>
        <el-button type="danger" @click.native="submit_deleteAll">删除</el-button>
      </div>
    </el-row>
    <!-- 表格数据展示 -->
    <div class="table">
      <el-table
        ref="eltableHouse"
        style="margin-top:20px;"
        :data="newlistData.slice((currentPage-1)*pagesize ,currentPage*pagesize )"
        @select="select"
        @select-all="selectAll"
      >
        <!-- table 的表格头 -->
        <template v-for="(item,index) in Columns">
          <compon-table :key="index" :item="item" v-bind="$attrs" />
        </template>
        <el-table-column label="状态" prop="zhuangtai">
          <template slot-scope="scope">
            <el-tag
              :type="scope.row.zhuangtai === '下架' ? 'primary' :'warning'"
              disable-transitions
            >{{ scope.row.zhuangtai }}</el-tag>
          </template>
        </el-table-column>

        <el-table-column fixed="right" width="240px" label="操作">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="handleEdit(scope.$index , scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页展示 -->
      <el-pagination
        class="pagination"
        v-bind="$attrs"
        :total="newlistData.length"
        :current-page="currentPage"
        :page-size="pagesize"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        v-on="$listeners"
      />
    </div>
    <!-- DIALOG数据信息 -->
    <div class="DIALOG">
      <!-- 查看数据信息 -->
      <el-dialog :visible.sync="LookFromVisible">
        <el-form :model="LookFrom" class="houseForm">
          <el-form-item label="运营方">
            <el-input v-model="LookFrom.formname" />
          </el-form-item>
          <el-form-item label="楼盘名称">
            <el-input v-model="LookFrom.formhouse" />
          </el-form-item>
          <el-form-item label="楼盘区域">
            <el-input v-model="LookFrom.formquyu" />
          </el-form-item>
          <el-form-item label="项目名称">
            <el-input v-model="LookFrom.formxiangmu" />
          </el-form-item>
          <el-form-item label="所属代理">
            <el-input v-model="LookFrom.formdaili" />
          </el-form-item>
          <el-form-item label="上市时间">
            <el-input v-model="LookFrom.Nowdata" />
          </el-form-item>
          <el-form-item label="当前状态">
            <el-select v-model="LookFrom.zhuangtai">
              <el-option label="上架" value="上架" />
              <el-option label="下架" value="下架" />
            </el-select>
          </el-form-item>
        </el-form>
        <div class="LookHousebutton" style="float:right;margin-top:-20px">
          <el-button type="primary" @click.native="submit_Edit">确定</el-button>
          <el-button type="waring" @click.native="submit_reset">重置</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getCity, getFlmHouse } from '@/api/api'
import ComponTable from '@/component/BaseTable/TableHeader'
export default {
  components: { ComponTable },
  data() {
    return {
      // DIALOG数据信息集合
      LookFromVisible: false, // DIALOG初始都不显示------查看信息集合
      LookFrom: {
        formname: '',
        formhouse: '',
        formquyu: '',
        formxiangmu: '',
        formdaili: '',
        Nowdata: '',
        zhuangtai: ''
      },
      // 表格头部api集合--------------
      houseArea: '',
      houseName: '',
      houseTai: '',
      option: [
        {
          value: '上架',
          label: '上架'
        },
        {
          value: '下架',
          label: '下架'
        }
      ],
      // 表格Select可选
      SelectData: [],
      // 要删除的数据集合
      deleteList: [],
      // 分页api集合-----------------
      currentPage: 1,
      pagesize: 10,
      // 数据api集合-----------------
      regionList: [],
      // 定义一个集合用于存放拷贝所有的数据
      oldListData: [],
      // 定义一个新集合用于处理所有的数据
      newlistData: [],
      // table表格头
      Columns: [
        {
          type: 'selection',
          width: '50'
        },
        {
          label: '运营方',
          prop: 'formname',
          width: '100'
        },
        {
          label: '楼盘名称',
          prop: 'formhouse'
        },
        {
          label: '楼盘区域',
          prop: 'formquyu',
          width: '150'
        },

        {
          label: '项目名称',
          prop: 'formxiangmu',
          width: '150'
        },

        {
          label: '所属代理',
          prop: 'formdaili',
          width: '100'
        },

        {
          label: '上市时间',
          prop: 'Nowdata',
          width: '150',
          sortable: 'true'
        }
      ]
    }
  },
  mounted() {
    this.getregionList()
    this.getHouseData()
  },
  methods: {
    // 表格头部数据方法集合-----------------------
    submit_search() {
      // 所在区域
      const area = {}
      this.regionList.filter(item => {
        if (item.value === this.houseArea[0]) {
          item.children.filter(items => {
            if (items.value === this.houseArea[1]) {
              area.formquyu = item.label + items.label
            }
          })
        }
      })
      if (!this.houseName && !this.houseTai && !this.houseArea) {
        this.$message.error('嘤嘤嘤~~请携带参数')
        return
      }
      const data = this.oldListData.filter(item => {
        if (this.houseName && this.houseArea && this.houseTai) {
          return (
            item.formhouse === this.houseName &&
            item.formquyu === area.formquyu &&
            item.zhuangtai === this.houseTai
          )
        } else if (this.houseName && this.houseArea) {
          return (
            item.formhouse === this.houseName && item.formquyu === area.formquyu
          )
        } else if (this.houseArea && this.houseTai) {
          return (
            item.formquyu === area.formquyu && item.zhuangtai === this.houseTai
          )
        } else if (this.houseName && this.houseTai) {
          return (
            item.formhouse === this.houseName &&
            item.zhuangtai === this.houseTai
          )
        } else if (this.houseName || this.houseArea || this.houseTai) {
          return (
            item.formhouse === this.houseName ||
            item.formquyu === area.formquyu ||
            item.zhuangtai === this.houseTai
          )
        }
      })
      this.newlistData = data
    },
    submit_deleteAll() {
      if (this.SelectData.length) {
        this.deleteList = this.deleteList.concat(this.SelectData)
        this.SelectData = []
        this.$message.success('嘤嘤嘤~~删除成功')
      } else {
        this.$message.warning('嘤嘤嘤~~请选择删除目标')
      }

      this.newlistData = this.oldListData.filter(item => {
        let isDel = false
        for (let i = 0; i < this.deleteList.length; i++) {
          if (item.date === this.deleteList[i].date) {
            isDel = true
            break
          }
        }
        if (!isDel) {
          return item
        }
      })
    },
    // 获取城市信息
    getregionList() {
      getCity().then(respone => {
        const data = this.formateCity(respone)
        this.regionList = data
      })
    },
    // 对城市信息做处理
    formateCity(cityAll) {
      const province = []
      const city = []
      // 格式化后 省市区
      const fmCity = []

      for (const key in cityAll) {
        const element = cityAll[key]
        if (key === '86') {
          province.push(element)
          for (const key in element) {
            const _element = element[key]
            const o = {
              value: key,
              label: _element,
              number: key
            }
            fmCity.push(o)
          }
        } else {
          fmCity.forEach(item => {
            if (key === item.number) {
              let o
              item.children = []
              for (const _city in element) {
                const _cityName = element[_city]
                city.push(element)
                o = {
                  value: _city,
                  label: _cityName,
                  number: _city,
                  isLeaf: true
                }
                item.children.push(o)
              }
            }
          })
        }
      }
      return fmCity
    },
    // 获取表格数据集合---------------------------------
    getHouseData() {
      getFlmHouse().then(respone => {
        this.oldListData = respone.data.data
        this.newlistData = this.oldListData.filter(item => {
          return item
        })
      })
    },
    // 表格的table -Select: check事件
    select(val) {
      this.SelectData = val
    },
    selectAll(val) {
      this.SelectData = val
    },
    handleChange(value) {},
    // 分页方法集合------------------------
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange: function(size) {
      this.pagesize = size
    },
    handleCurrentChange: function(currentPage) {
      this.currentPage = currentPage
    },
    // 操作按钮--------------查看信息数据集合
    handleEdit(index, item) {
      this.LookFrom = item
      this.LookFromVisible = true
    },
    handleDelete(index, item) {
      this.newlistData.splice(index, 1)
    },
    submit_Edit() {
      this.$message.success('嘤嘤嘤~~编辑成功')
      this.LookFromVisible = false
    },
    submit_reset() {
      this.$message.success('嘤嘤嘤~~重置成功')
      this.LookFrom.formname = ''
      this.LookFrom.formhouse = ''
      this.LookFrom.formquyu = ''
      this.LookFrom.formxiangmu = ''
      this.LookFrom.formdaili = ''
      this.LookFrom.Nowdata = ''
      this.LookFrom.zhuangtai = ''
    }
  }
}
</script>

<style scoped>
.houseManage {
  padding-top: 20px;
}
.header_hose {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  padding-left: 20px;
}
.house_header_region {
  height: 40px;
}

.pagination {
  position: relative;
  left: 70%;
}
.houseForm /deep/ .el-form-item /deep/ .el-form-item__label {
  width: 100px;
  text-align: center;
}
.houseForm /deep/ .el-form-item /deep/ .el-form-item__content /deep/ .el-input {
  width: 200px;
}
</style>
